
.Background
{
    background: #2A2A2A;
    
}

.navnav {
    background:#2A2A2A;
    top: 0; /* Position the navbar at the top of the page */
    left:0;
    margin: 0px;
    padding:0px;
    width: 100%; /* Full width */
    right: 0;
  }

#wrapper {
    margin-left: auto;
    margin-right: auto;
    justify-content: end;
    width: 960px;
  }

  .CatalogImages {
    width: 100%;
    height: 20rem;
  }

  .nav-item, .nav-item-footer {
    text-decoration: none;
    position: relative;
  }

  .nav-item::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: white;

    transform: scale(0);
    transform-origin: right;
    transition: transform 250ms ease-in;
  }

  .nav-item:hover::after {
    transform: scaleX(1);
    transform-origin: left;
    height: 5px;
  }

  .nav-item-footer::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: white;

    transform: scale(0);
    transform-origin: right;
    transition: transform 250ms ease-in;

  }

  .nav-item-footer:hover::after {
    transform: scaleX(1);
    transform-origin: left;
  }

  textarea {
    resize: none;

    width: 20px;
    min-width: 200px;
    max-width: auto;

    height: 250px;
    min-height: 250px;
    max-height: auto;
  }
  
  .shortTextArea
  {
    width: 400px;
    min-width: 400px;
    max-width: auto;
  }

  .imageFromUser
  {
    width: 500px;
    min-width: 500px;
    max-width: auto;
  }

  .justify
  {
    text-align: justify;
    text-justify: inter-word;
  }

.hamburger{
  display: none;
}

@media (max-width: 767px)  
{
  .hamburger{
    display: fixed;
    padding-top: 10px;
    margin-left: 10px;
    z-index: 10;
  }
}

